@media screen and (max-width: 2100px) {
 .header {

  &-logo {
   width: 140px;
   padding: 15px 16px;
   font-size: 29px;
   line-height: 30px;
  }

  &-menu {
   &-item {
    margin-right: 60px;
   }
  }

  &-btn {
   padding: 10px 40px;
  }
 }
}

@media screen and (max-width: 1440px) {
 .header {
  justify-content: space-around;
 }
}

@media screen and (max-width: 860px) {
 .header {
  flex-direction: column;

  &-logo {
   align-self: center;
  }

  &-menu {
   flex-direction: column;
   justify-content: center;

   &-item {
    display: block;
    margin: 15px 0;
   }
  }

  &-btn {
   padding: 8px 51px;
  }
 }
}